n  .is-invisible-fullhd\n    visibility: hidden !important\n","/* Bulma Layout */\n@charset \"utf-8\"\n\n@import \"hero\"\n@import \"section\"\n@import \"footer\"\n","@import \"../utilities/mixins\"\n\n$hero-body-padding: 3rem 1.5rem !default\n$hero-body-padding-small: 1.5rem !default\n$hero-body-padding-medium: 9rem 1.5rem !default\n$hero-body-padding-large: 18rem 1.5rem !default\n\n$hero-colors: $colors !default\n\n// Main container\n.hero\n  align-items: stretch\n  display: flex\n  flex-direction: column\n  justify-content: space-between\n  .navbar\n    background: none\n  .tabs\n    ul\n      border-bottom: none\n  // Colors\n  @each $name, $pair in $hero-colors\n    $color: nth($pair, 1)\n    $color-invert: nth($pair, 2)\n    &.is-#{$name}\n      background-color: $color\n      color: $color-invert\n      a:not(.button):not(.dropdown-item):not(.tag):not(.pagination-link.is-current),\n      strong\n        color: inherit\n      .title\n        color: $color-invert\n      .subtitle\n        color: bulmaRgba($color-invert, 0.9)\n        a:not(.button),\n        strong\n          color: $color-invert\n      .navbar-menu\n        +touch\n          background-color: $color\n      .navbar-item,\n      .navbar-link\n        color: bulmaRgba($color-invert, 0.7)\n      a.navbar-item,\n      .navbar-link\n        &:hover,\n        &.is-active\n          background-color: bulmaDarken($color, 5%)\n          color: $color-invert\n      .tabs\n        a\n          color: $color-invert\n          opacity: 0.9\n          &:hover\n            opacity: 1\n        li\n          &.is-active a\n            opacity: 1\n        &.is-boxed,\n        &.is-toggle\n          a\n            color: $color-invert\n            &:hover\n              background-color: bulmaRgba($scheme-invert, 0.1)\n          li.is-active a\n            &,\n            &:hover\n              background-color: $color-invert\n              border-color: $color-invert\n              color: $color\n      // Modifiers\n      @if type-of($color) == 'color'\n        &.is-bold\n          $gradient-top-left: darken(saturate(adjust-hue($color, -10deg), 10%), 10%)\n          $gradient-bottom-right: lighten(saturate(adjust-hue($color, 10deg), 5%), 5%)\n          background-image: linear-gradient(141deg, $gradient-top-left 0%, $color 71%, $gradient-bottom-right 100%)\n          +mobile\n            .navbar-menu\n              background-image: linear-gradient(141deg, $gradient-top-left 0%, $color 71%, $gradient-bottom-right 100%)\n  // Sizes\n  &.is-small\n    .hero-body\n      padding: $hero-body-padding-small\n  &.is-medium\n    +tablet\n      .hero-body\n        padding: $hero-body-padding-medium\n  &.is-large\n    +tablet\n      .hero-body\n        padding: $hero-body-padding-large\n  &.is-halfheight,\n  &.is-fullheight,\n  &.is-fullheight-with-navbar\n    .hero-body\n      align-items: center\n      display: flex\n      & > .container\n        flex-grow: 1\n        flex-shrink: 1\n  &.is-halfheight\n    min-height: 50vh\n  &.is-fullheight\n    min-height: 100vh\n\n// Components\n\n.hero-video\n  @extend %overlay\n  overflow: hidden\n  video\n    left: 50%\n    min-height: 100%\n    min-width: 100%\n    position: absolute\n    top: 50%\n    transform: translate3d(-50%, -50%, 0)\n  // Modifiers\n  &.is-transparent\n    opacity: 0.3\n  // Responsiveness\n  +mobile\n    display: none\n\n.hero-buttons\n  margin-top: 1.5rem\n  // Responsiveness\n  +mobile\n    .button\n      display: flex\n      &:not(:last-child)\n        margin-bottom: 0.75rem\n  +tablet\n    display: flex\n    justify-content: center\n    .button:not(:last-child)\n      +ltr-property(\"margin\", 1.5rem)\n\n// Containers\n\n.hero-head,\n.hero-foot\n  flex-grow: 0\n  flex-shrink: 0\n\n.hero-body\n  flex-grow: 1\n  flex-shrink: 0\n  padding: $hero-body-padding\n","@import \"../utilities/mixins\"\n\n$section-padding: 3rem 1.5rem !default\n$section-padding-medium: 9rem 1.5rem !default\n$section-padding-large: 18rem 1.5r